import React, { useState, useEffect, useCallback, useContext, createContext } from "react"
import pt from "prop-types"
import AppBar from "./../components/AppBar"
import AppFotter from "./../components/AppFotter"
import Loading from "../components/Loading"
import SideBar from "../components/SideBar"
import useLocalStorage from "../hooks/useLocalStorage"
import Router from "../routes/router"
import { deepMerge } from "grommet/utils"
import { Grommet, ResponsiveContext, Box, grommet, ThemeContext } from "grommet"
import { AppContext } from "./app-context"
import { users } from "./../mocks/users"

const Shell = ({ appInitialized }) => {
  const [menuOpen, setMenuOpen] = useState(false)
  const [theme, setTheme] = useState(grommet)
  const [isDark, setIsDark] = useLocalStorage("isDarkMode", true)
  const [color, setColor] = useState("#3C3B6E")
  const [initialized, setInitialized] = useState(appInitialized)
  const [user, setUser] = useState()
  useEffect(() => {}, [isDark])

  useEffect(() => {
    // this should check if the currentUser is logged in and return a user object if so
    const getUser = () => {
      setUser({ ...users[0], authenticated: true })
    }
    getUser()

    setInitialized(appInitialized)
  }, [appInitialized])

  const [, updateState] = useState()
  const forceUpdate = useCallback(() => updateState({}), [])

  useEffect(() => {
    forceUpdate()
  }, [forceUpdate])

  useEffect(() => {
    const update = () => {
      const updatedTheme = deepMerge(grommet, {
        list: {
          item: {
            pad: { horizontal: "xsmall", vertical: "xsmall" },
            background: "light-1",
            hover: { background: "red" },
            style: { fontWeight: "normal" },
          },
        },
        global: {
          colors: {
            brand: color,
            focus: color,
            "light-1": "#FFF",
          },
        },
      })
      setTheme(updatedTheme)
    }
    update()
  }, [color])

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Grommet
        theme={theme}
        background={{
          color: "light-1",
          dark: isDark === true ? true : false,
        }}
        style={{ flex: "0 0 auto", height: "100vh" }}
      >
        <ResponsiveContext.Consumer>
          {size => {
            return (
              <AppContext.Provider value={{ appSize: size }}>
                <Box fill>
                  <>
                    <AppBar
                      onMenuClick={() => {
                        setMenuOpen(!menuOpen)
                      }}
                      userDetails={user}
                    />
                    <Box
                      direction="row"
                      flex
                      animation={[
                        { type: "fadeIn", duration: 300 },
                        { type: "slideRight", size: "xlarge", duration: 150 },
                      ]}
                    >
                      <SideBar
                        expanded={menuOpen}
                        onModeChange={isDark => {
                          setIsDark(isDark)
                        }}
                        onClickOutside={() => {
                          setMenuOpen(false)
                        }}
                      />
                      <Box overflow={{ vertical: "scroll" }} pad={0} flex>
                        {initialized ? <Router /> : <Loading />}
                      </Box>
                    </Box>
                    <AppFotter></AppFotter>
                  </>
                </Box>
              </AppContext.Provider>
            )
          }}
        </ResponsiveContext.Consumer>
      </Grommet>
    </div>
  )
}

Shell.propTypes = {
  appInitialized: pt.bool,
  theme: pt.object,
}
export default Shell
