import React from "react"
import pt from "prop-types"
import { Box, Button, Avatar, Heading } from "grommet"
import { Menu, Language } from "grommet-icons"

const AppBar = ({ onMenuClick, userDetails }) => {
  const authenticated = userDetails?.authenticated || false

  return (
    <Box
      tag="header"
      pad={{ left: "small" }}
      background="light-1"
      direction="row"
      align="center"
      style={{
        borderBottom: "1px solid #DDD",
        zIndex: "1",
      }}
    >
      <Button icon={<Menu />} onClick={onMenuClick}></Button>
      <Box pad={{ left: "medium" }} direction="row">
        <Heading
          level="2"
          weight="bold"
          color="#B22234"
          style={{ lineHeight: "12px" }}
          margin={{ top: 0, bottom: 0, right: 3 }}
        >
          Realtor
        </Heading>
        <Heading
          level="2"
          weight="bold"
          color="brand"
          style={{ lineHeight: "12px" }}
          margin={{ top: 0, bottom: 0, right: "small", left: "3px" }}
        >
          Finder
        </Heading>
      </Box>
      {authenticated && (
        <Box margin={{ left: "auto" }} direction="row" gap="xxsmall">
          <Avatar margin={"small"} style={{ border: "1px solid #DDD" }} src={userDetails.Image} />
          {userDetails.Image ? undefined : userDetails.Name.split(" ").map(n => n.charAt(0))}
        </Box>
      )}
    </Box>
  )
}

AppBar.propTypes = {
  onMenuClick: pt.func,
}

export default AppBar
